.menu {
  position: fixed;
  padding: 24px 16px;
  overflow-y: auto;
  max-width: 440px;
  width: 100%;
  box-sizing: border-box;
  height: 100vh;
  font-family: Lakes-medium, Arial, Helvetica, sans-serif;
  font-weight: 600;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2), -2px 0px 4px #454f7e05; }
  .menu__header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
  .menu__link-logo {
    margin-right: auto;
    margin-left: 16px; }
  .menu-nav {
    margin: 24px 0; }
    .menu-nav__list {
      list-style: none;
      margin: 0;
      padding: 0;
      position: relative; }
    .menu-nav__item {
      margin-bottom: 14px; }
    .menu-nav__link {
      text-decoration: none;
      color: black; }
    .menu-nav__link:hover, .menu-nav__link:focus, .menu-nav__link:active {
      color: #ff3e79;
      transition: 0.5s; }
    .menu-nav__link:hover::before {
      content: "";
      height: 26px;
      align-self: center;
      border-left: 4px solid #41f6d7;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      position: absolute;
      left: -16px; }
  .menu-footer {
    padding: 0 0 24px 0;
    display: flex;
    flex-direction: column; }
    .menu-footer__btn-user, .menu-footer__btn-chat {
      margin-left: 16px; }
    .menu-footer__tel {
      font-size: 24px;
      line-height: 32px;
      margin: 6px 0 6px 0;
      text-decoration: none;
      color: black; }
      .menu-footer__tel:hover {
        color: #ff3e79;
        transition: 0.5s; }
    .menu-footer__email {
      text-decoration: none;
      color: black;
      margin: 6px 0 0 0; }
      .menu-footer__email:hover {
        color: #ff3e79;
        transition: 0.5s; }
  .menu .block-language__list {
    list-style: none;
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0; }
  .menu .block-language__item--active {
    color: #b4a9a9; }
  .menu .block-language__list li {
    margin-right: 16px;
    cursor: pointer; }
    .menu .block-language__list li:hover {
      color: #ff3e79;
      transition: 0.5s; }

@media screen and (min-width: 768px) {
  .menu-nav__list {
    margin-bottom: 268px; }
  .menu-nav__item {
    margin-bottom: 28px; } }

@media screen and (min-width: 1440px) {
  .body-wrapper {
    display: flex;
    flex-direction: row;
    background-color: #f8f8f8;
    justify-content: center; }
  .menu {
    position: relative;
    max-width: 320px;
    height: inherit;
    bottom: inherit;
    background-color: white;
    z-index: 2;
    box-shadow: none;
    border-right: 1px solid #d9fff5; }
    .menu__link-logo {
      margin-left: 0; }
  .menu__btn-exit--clicked-exit {
    display: none; } }
