.modal {
  padding: 24px 16px;
  max-width: 440px;
  overflow-y: auto;
  height: 100vh;
  font-family: Lakes-medium, Arial, Helvetica, sans-serif;
  font-weight: 600;
  position: fixed;
  background-color: white;
  top: 0;
  right: 0;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2), -2px 0px 4px rgba(69, 79, 126, 0.02); }
  .modal__page {
    display: flex;
    flex-direction: column; }
  .modal__title {
    font-size: 24px;
    line-height: 32px;
    font-family: Lakes-bold, Arial, Helvetica, sans-serif; }
    .modal__title:before {
      content: "";
      height: 38px;
      border-left: 4px solid #41f6d7;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      position: absolute;
      left: 0; }
  .modal__form-container {
    max-width: 376px;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto; }
  .modal__form {
    box-sizing: border-box;
    max-width: 376px;
    width: 100%;
    height: 48px;
    border: 1px solid #eaeaea;
    border-radius: 8px;
    padding: 12px 16px;
    margin-bottom: 16px; }
    .modal__form-text {
      height: 118px; }
    .modal__form-info {
      font-size: 14px;
      line-height: 16px;
      color: #7e7e82; }
  .modal__btn-send {
    margin-top: 10px;
    margin-left: auto;
    padding: 8px 18px; }
  .modal__link-data, .modal__link-politics {
    text-decoration: none;
    color: #ff3e79; }

.btn-oval {
  width: 136px;
  height: 32px;
  background-color: #ff3e79;
  color: white;
  font-family: Lakes-bold;
  text-transform: uppercase;
  border-radius: 16px;
  border: none;
  background-repeat: no-repeat;
  background-position: 85% 50%;
  background-image: url(./static/arrow-right.svg);
  display: flex; }

@media screen and (min-width: 440px) {
  .modal__title {
    margin-left: 15px; } }
