@charset "UTF-8";
.page-wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1120px;
  padding: 0 16px 24px 16px; }

.service {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column; }
  .service__text-second {
    display: none; }
  .service__title-container {
    display: flex;
    justify-content: space-between;
    padding: 24px; }
  .service__title {
    margin: 0;
    font-size: 28px;
    line-height: 40px;
    font-family: Lakes-bold, Arial, Helvetica, sans-serif; }
    .service__title:before {
      content: "";
      height: 38px;
      align-self: center;
      border-left: 4px solid #41f6d7;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      position: absolute;
      left: 0; }
  .service__nav {
    overflow-x: auto;
    margin-bottom: 16px; }
  .service__list {
    display: flex;
    list-style: none;
    justify-content: space-between;
    padding: 0; }
  .service__item {
    color: #7e7e82;
    text-decoration: none;
    padding: 8px 22px;
    white-space: nowrap;
    font-weight: 600; }
  .service__item:hover, .service__item:focus, .service__item:active {
    border: 2px solid #b8ffec;
    border-radius: 6px;
    background: #ffffff;
    padding: 8px 20px;
    transition: 0.5s; }
  .service__info {
    font-size: 14px;
    line-height: 20px; }
  .service__img-cps {
    object-fit: cover;
    height: 100%;
    width: 100%;
    border-radius: 5px; }

@media screen and (min-width: 768px) {
  .service__content {
    display: flex;
    flex-direction: row; }
  .service__info {
    padding-right: 48px;
    max-width: 50%; } }

@media screen and (min-width: 1200px) {
  .page-wrapper {
    margin-left: auto;
    margin-right: auto; } }

@media screen and (min-width: 1440px) {
  .service__title {
    position: relative; }
    .service__title:before {
      content: "";
      height: 38px;
      align-self: center;
      border-left: 4px solid #41f6d7;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      position: absolute;
      left: -38px; }
  .service__nav {
    overflow-x: visible; }
  .service__item {
    padding: 0; }
  .service__btn-circle {
    margin: 0 8px; }
  .service__btn-circle--back-repair {
    position: relative;
    margin-right: 140px !important; }
  .service__btn-circle--back-checkstatus {
    position: relative; }
  .service__btn-circle--back-repair::after {
    content: "Оставить заявку";
    right: 50px;
    top: 10px;
    position: absolute;
    font-size: 16px;
    line-height: 24px;
    white-space: nowrap; }
  .service__btn-circle--back-checkstatus::after {
    content: "Статус ремонта";
    right: 50px;
    top: 10px;
    position: absolute;
    font-size: 16px;
    line-height: 24px;
    white-space: nowrap; }
  .btn-circle--back-repair {
    display: inline; }
  .btn-circle--back-checkstatus {
    display: inline;
    margin-right: 0; } }

p {
  margin: 0; }
