.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 24px 16px; }
  .header__link-logo {
    margin-right: auto;
    margin-left: 8px; }
  .header__btn-circle {
    margin: 0 8px; }
  .header__linear {
    border-left: 2px solid #eaeaea;
    margin: 0 8px; }

.btn-circle--mod-disabled {
  display: none; }

.btn-circle--back-checkstatus {
  margin-right: 0; }

@media screen and (min-width: 768px) {
  .btn-circle--back-call, .btn-circle--back-chat, .btn-circle--back-user {
    display: inline; }
  .header__panel {
    display: flex; } }

@media screen and (min-width: 1440px) {
  .header {
    display: none; } }
